














































import { Component, Vue } from 'vue-property-decorator';

import Attention from '@/components/home/Attention.vue';
import LiveBroadCast from '@/components/home/LiveBroadCast.vue';
import Game from '@/components/home/Game.vue';
import Castle from '@/components/home/Castle.vue';
import HomeNewsPopup from '@/components/home/HomeNewsPopup.vue';
import NoticePopop from '@/components/common/NoticePopop.vue';


@Component({
  components: {
    Attention,
    LiveBroadCast,
    Game,
    Castle,
    HomeNewsPopup,
    NoticePopop
  },
})

export default class Home extends Vue {
  private show?: boolean = true;
  private tabActive?: number = 1;
  private showNewsPopup?: boolean = true;
  private showNoticePopup?: boolean = false;
  private noticeText?: string = '稳定、快速、笔笔充值100元以商还可赠送限量座骑哦!';
  private isLoading?: boolean = false;
  private loading?: boolean = false;
  private finished?: boolean = false;
  private tabList?: any[] = [
    '关注',
    '直播',
    '游戏',
    '城堡',
  ];
  private changeTab(index: number): void {
    this.tabActive = index;
  }
  public showNoticePopover() {
    this.showNoticePopup = true;
  }
  private hidePopup(v: boolean) {
    this.showNoticePopup = v;
  }
  private closeNewsPopup(v: boolean) {
    this.showNewsPopup = v;
    this.$store.dispatch('setNewsClose', true);
  }
  private goPage(url: string) {
    this.$router.push(url);
  }
  private onLoad(): void {
    setTimeout(() => {
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
  private onRefresh () {
    setTimeout(() => {
      this.isLoading = false;
    }, 1000);
  }
  private mounted() {
    if (this.$store.state.newsClose == true) {
      this.showNewsPopup = false;
    }
  }
}
