



























import { Component, Vue, Prop } from "vue-property-decorator";

@Component({})
export default class ImageDropLets extends Vue {
  @Prop({ type: Array, default: [] }) public readonly itemData!: any[];
  @Prop({ type: String, default: '/live' }) public readonly toPage!: string;
  @Prop({ type: String, default: 'home' }) public readonly imgPath!: string;
  private getlotteryBg(img: string): string {
    return `background: url(${require('@/assets/img/'+this.imgPath+'/'+img+'')});
            background-repeat: no-repeat;
            background-size: 100% 100%;`;
  }
  private goPage(url: string) {
    this.$router.push(url);
  }
}
